import axios from 'axios'
import * as qs from 'qs'

import { timeNumberEnum } from '@/constants'

import { BASE_URL, errorHandler } from './config'

const instance = axios.create({
  baseURL: BASE_URL,
  timeout: timeNumberEnum.timeout
})

// è¯·æ±‚æ‹¦æˆª
// qs åºåˆ—åŒ– é˜²æ­¢XSRFæ”»å‡» å¯ä»¥å¯¹æ·±å±‚æ¬¡çš„json arrayè¿›è¡Œåºåˆ—åŒ–
instance.interceptors.request.use(
  (config: any) => {
    config.transformRequest = [
      (data) => {
        return qs.stringify(data, {
          allowDots: true
        })
      }
    ]

    // `paramsSerializer` æ˜¯ä¸€ä¸ªè´Ÿè´£ `params` åºåˆ—åŒ–çš„å‡½æ•°
    config.paramsSerializer = (params) => {
      return qs.stringify(params, {
        arrayFormat: 'repeat'
      })
    }

    return config
  },
  async (error) => {
    return await Promise.reject(error)
  }
)

// å“åº”æ‹¦æˆª
instance.interceptors.response.use(
  (res) => {
    return res.data
  },
  (err) => {
    if (err) {
      errorHandler(err)
    }
    return err
  }
)

export default instance
